import React from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { Root, getHeader, getStandardScheme } from "@mui-treasury/layout";
import { Switch, Link, Route } from "react-router-dom";
import About from "./components/About";
import Project from "./components/Project";
import { Resume } from "./components/Resume";

const Header = getHeader(styled);

const standardScheme = getStandardScheme();

let theme = createMuiTheme({
  palette: {
    primary: {
      main: "#000",
    },
    secondary: {
      main: "#ECF0F1",
    },
  },
  overrides: {
    MuiListItem: {
      root: {
        "&.Mui-selected": {
          backgroundColor: "#7d4709",
          color: "#fff",
          "& svg": {
            color: "#fff",
          },
          "&:hover": {
            backgroundColor: "#95550b",
          },
        },
      },
    },
    MuiListItemIcon: {
      root: {
        minWidth: 48,
      },
    },
  },
  typography: {
    h3: {
      fontSize: 40,
      fontFamily: "'Montserrat', sans-serif",
    },
    h2: {
      fontFamily: "'Niconne', cursive",
    },
    body1: {
      fontFamily: "'Forum', cursive",
      fontSize: "1.3rem",
    },
    h4: {
      fontFamily: "'Fredoka One', cursive",
    },
  },
});

theme = responsiveFontSizes(theme);

const useStyles = makeStyles((theme) => ({
  textContainer: {
    color: "black",
    textDecoration: "none",
    "&:active": {
      color: "#7d4709",
    },
  },
  headingContainer: {
    textAlign: "center",
  },
  drawerContainer: {
    marginTop: "100px",
    backgroundColor: "#ECF0F1",
  },
  linkContainer: {
    color: "black",
    textDecoration: "none",
    "&:hover": {
      color: "#707B7C",
    },
  },
}));

const CustomThemeDemo = () => {
  // by default, Root will render ThemeProvider, pass "theme" to override default theme
  // If you want to use your own ThemeProvider, pass "themeProvidedOmitted" to Root and wrapped it with your ThemeProvider
  // note: If you set "themeProvidedOmitted=true" to Root without your own ThemeProvider, you will see error "cannot read palette of undefined"
  const classes = useStyles();

  return (
    <Root theme={theme} scheme={standardScheme}>
      <CssBaseline />
      <Header color={"secondary"} style={{ width: "100%", position: "fixed" }}>
        <Toolbar>
          <Grid container justify="center">
            <Grid item xs={4} className={classes.headingContainer}>
              <Typography variant="h4">
                <Link to="/" className={classes.linkContainer}>
                  Home
                </Link>
              </Typography>
            </Grid>
            <Grid item xs={4} className={classes.headingContainer}>
              <Typography variant="h4">
                <Link to="/project" className={classes.linkContainer}>
                  Projects
                </Link>
              </Typography>
            </Grid>
            <Grid item xs={4} className={classes.headingContainer}>
              <Typography variant="h4">
                <Link to="/resume" className={classes.linkContainer}>
                  Resume
                </Link>
              </Typography>
            </Grid>
          </Grid>
        </Toolbar>
      </Header>
      <div
        style={{
          marginLeft: "20px",
          marginTop: "60px",
          marginRight: "20px",
        }}
      >
        <Switch>
          <Route exact path="/">
            <About />
          </Route>
          <Route exact path="/project">
            <Project />
          </Route>
          <Route exact path="/resume">
            <Resume />
          </Route>
        </Switch>
      </div>
    </Root>
  );
};

export default CustomThemeDemo;
