import React, { Fragment } from "react";
import Grid from "@material-ui/core/Grid";
import { Typography } from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import { makeStyles } from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";
import Divider from "@material-ui/core/Divider";
import Grow from "@material-ui/core/Grow";
import Typewriter from "typewriter-effect";
import {
  SocialProvider,
  SocialLink,
} from "@mui-treasury/components/socialLink";
import { useMoonSocialLinkStyles } from "@mui-treasury/styles/socialLink/moon";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: "10%",
  },
  avatarContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    [theme.breakpoints.only("xs")]: {
      marginBottom: "1.875em",
    },
  },
  chipContainer: {
    margin: theme.spacing(1),
    textAlign: "center",
  },
  typographyContainer: {
    textAlign: "center",
  },
  typographyText: {
    "&::selection": {
      color: "white",
      background: "#FC4A37",
    },
    "&::-moz-selection": {
      color: "white",
      background: "#FC4A37",
    },
  },
  nameContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    marginBottom: "10px",
  },
  iconContainer: {
    margin: theme.spacing(1),
    color: "black",
  },
  contactContainer: {
    marginTop: "10px",
    textAlign: "center",
  },
  avatarImage: {
    width: "18.75rem",
    height: "18.75rem",
    boxShadow: `12px 12px 24px 0 rgba(0, 0, 0, 0.1),
    -12px -12px 24px 0 rgba(255, 255, 255, 3)`,
    cursor: "pointer",
  },
  skillText: {
    fontWeight: 600,
    cursor: "pointer",
  },
}));

const skillImage = {
  TYPESCRIPT: require("../../assets/skillSvg/typescript.svg"),
  JAVASCRIPT: require("../../assets/skillSvg/javascript.svg"),
  HTML: require("../../assets/skillSvg/html5.svg"),
  CSS: require("../../assets/skillSvg/css3.svg"),
  REACT: require("../../assets/skillSvg/reactjs.svg"),
  REACT_NATIVE: require("../../assets/skillSvg/reactjs.svg"),
  GRAPHQL: require("../../assets/skillSvg/graphql.svg"),
  NODEJS: require("../../assets/skillSvg/nodejs.svg"),
  MONGODB: require("../../assets/skillSvg/mongodb.svg"),
  POSTGRESQL: require("../../assets/skillSvg/sql.svg"),
  GIT: require("../../assets/skillSvg/git.svg"),
  DOCKER: require("../../assets/skillSvg/docker.svg"),
};

const About = () => {
  const classes = useStyles();
  const [checked, setChecked] = React.useState(false);

  React.useEffect(() => {
    setChecked((prev) => !prev);
    document.title = "Home";
  }, []);

  return (
    <Fragment>
      <Grid container className={classes.root}>
        <Grow
          in={checked}
          style={{ transformOrigin: "0 0 0" }}
          {...(checked ? { timeout: 1000 } : {})}
        >
          <Grid item xs={12} sm={6} className={classes.avatarContainer}>
            <Avatar
              src={require("../../assets/profileImage.svg")}
              className={classes.avatarImage}
            />
          </Grid>
        </Grow>

        <Grow
          in={checked}
          style={{ transformOrigin: "0 0 0" }}
          {...(checked ? { timeout: 1000 } : {})}
        >
          <Grid item xs={12} sm={6}>
            <Grid container direction="column">
              <Grid item className={classes.nameContainer}>
                <Typography variant="h2">
                  <Typewriter
                    options={{
                      strings: ["Prashant Kumar", "Hello! Nice to see you. ðŸ‘‹"],
                      autoStart: true,
                      loop: true,
                      cursor: "",
                      delay: 15,
                    }}
                  />
                </Typography>
              </Grid>

              <Grid item className={classes.chipContainer}>
                {[
                  "TYPESCRIPT",
                  "JAVASCRIPT",
                  "HTML",
                  "CSS",
                  "REACT",
                  "REACT_NATIVE",
                  "GRAPHQL",
                  "NODEJS",
                  "MONGODB",
                  "POSTGRESQL",
                  "GIT",
                  "DOCKER",
                ].map((name, index) => (
                  <Chip
                    avatar={<Avatar alt="Natacha" src={skillImage[name]} />}
                    className={classes.skillText}
                    label={name === "REACT_NATIVE" ? "REACT NATIVE" : name}
                    key={index}
                    style={{ marginRight: "10px", marginBottom: "4px" }}
                  />
                ))}
              </Grid>
              <Grid
                item
                //style={{ backgroundColor: "#EC7063" }}
                className={classes.typographyContainer}
              >
                <Typography variant="body1" className={classes.typographyText}>
                  I'm an avid follower of technology, a developer and programmer
                  who loves learning new tech skills and I'm always looking for
                  something new to challenge myself.I passionate about
                  developing products that have positive real life impact. As a
                  javascript junkie i have worked on MERN stack and with several
                  other technologies, while keeping a holistic perspective in
                  mind.
                </Typography>
              </Grid>
              <Divider style={{ marginTop: "10px" }} />
              <Grid item className={classes.contactContainer}>
                <SocialProvider useStyles={useMoonSocialLinkStyles}>
                  <SocialLink
                    brand={"Instagram"}
                    className={classes.iconContainer}
                    href={"https://www.instagram.com/prashantjdreww"}
                  />

                  <SocialLink
                    brand={"Envelope"}
                    className={classes.iconContainer}
                    href="mailto:prashantdrew23@gmail.com"
                  />
                  <SocialLink
                    brand={"LinkedIn"}
                    className={classes.iconContainer}
                    href={"https://www.linkedin.com/in/prashantkumar23"}
                  />
                </SocialProvider>
              </Grid>
            </Grid>
          </Grid>
        </Grow>
      </Grid>
    </Fragment>
  );
};

export default About;
